<template>
<section class="login-page">
  <header>
    <el-row>
      <el-col :span="4">
        <img src="../assets/images/joy.png" />
      </el-col>
      <el-col :span="20">
        <h2>SOHO</h2>
      </el-col>
    </el-row>
  </header>
  <main>
    <el-form ref="form" :model="form" label-width="80px" v-loading.fullscreen.lock="logining">
      <el-form-item label="">
        xyf1215
      </el-form-item>
      <el-form-item label="">
        12331
      </el-form-item>
      <el-form-item label="CTI">
        <el-select placeholder="">
          <el-option label="" value="shanghai"></el-option>
          <el-option label="" value="beijing"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="login"></el-button>
      </el-form-item>
    </el-form>
  </main>
</section>
</template>

<script>
export default {
  name: 'login',
  data() {
    return {
      logining: false
    }
  },
  methods: {
    login() {
      this.logining = true
      setTimeout(() => {
        this.$router.replace('index')
      }, 2000)
    }
  }
}
</script>

<style lang="less" scoped>
section {
    margin: 10% auto 0;
    width: 400px;
    border: 1px solid #ddd;
    border-radius: 4px;
}

header {
    padding: 10px;
    border-bottom: 1px solid #ddd;
    img {
        height: 50px;
    }
    h2 {
        margin: 0;
        text-align: center;
        line-height: 50px;
    }
}

main {
    padding: 10px;
}

</style>



